                                <option value="-1">{{ $t('msg.random') }}</option>
                                <option v-for="quality in 4" :key="quality" :value="quality-1">{{ quality }}</option>
                            </select>
                        </div>
                        <div class="mt-3">
                            <div>{{ $t('msg.language') }}</div>
                            <select class="form-select form-select-sm" name="entryQuality" id="entryQuality"
