                                <input class="form-check-input" type="checkbox" :value="config" :id="'score-'+config"
                                    v-model="userSetting.scoreConfig.arrRule">
                                <label class="form-check-label" :for="'score-'+config">
                                    {{ this.$artifact.toChinese(config,"score") }}
