<script>
    import demoAlert from '../components/demo-alert'
    import artifactShow from '../components/artifact-show'
    import popup from '../components/popup'
    import artifactList from '../components/artifact-list'
    // bootstrap
    import 'bootstrap/js/dist/alert'
    import 'bootstrap/js/dist/modal'
    import 'bootstrap/js/dist/dropdown'

    export default {
        components: {
            demoAlert,
            artifactShow,
            popup,
            artifactList
        },
        data() {
            return {
                state: this.$store.state,
                showSymbol: "", // symbol
                showDetail: Object, // 
                ArtifactsList: [], // 
                cusCloseSwitch: true, // -modal
                cusSuit: "", // 
                cusPart: "", // 
                cusMainEntry: "", // 
                cusEntry: [], // 
                cusEntryRate: Object, // 
                cusEntryList: Object, // 
                userSetting: { // 
                    scoreConfig: { // 
                        mode: "string",
                        strRule: "default",
                        arrRule: [],
                    },
                    language: "zh", // 
                    entryQuality: -1, // 
                    highScore: 35, // 
                    listBriefMode: true, // details/brief
                    filterMain: "default", // 
                    filterPart: "default" // 
                },
                defaultSetting: "", // 
                alertFunc: {
                    alertShow: false, // 
                    alertMsg: String, // 
                    alertClose: Function, // 
                    alertState: "success" // 
                },
            }
        },
        created() {
            this.cusEntryList = {
                Plume: this.$artiConst.val.Plume,
                Flower: this.$artiConst.val.Flower,
                Sands: this.$artiConst.val.Sands,
                Circlet: this.$artiConst.val.Circlet,
                Goblet: this.$artiConst.val.Goblet
            };
            this.defaultSetting = JSON.stringify(this.userSetting);
            // 
            for (let i in this.$artiConst.val.entryValue) {
                this.cusEntryRate[i] = this.$artiConst.val.entryValue[i][this.$artiConst.val.entryValue[i].length - 1];
            }
        },
        mounted() {
            // 
            if (!window.localStorage) {
                alert("localstorage");
                return false;
            } else {
                if (localStorage.userSetting === undefined) {
                    localStorage.userSetting = this.defaultSetting;
                } else if (localStorage.userSetting !== '') {
                    let settingObj = JSON.parse(localStorage.getItem("userSetting"));
                    // 
                    Object.assign(this.userSetting, settingObj);
                }
            }
            this.changeSetting();
            // 
            if (this.ArtifactsList.length === 0 && this.$artifact.AUSList.length !== 0) {
                this.syncListData();
            }
            setTimeout(() => {
                this.showSymbol = this.$store.state.selectHistory;
            }, 1)
        },
        computed: {
            ArtifactScore() {
                let mode = this.userSetting.scoreConfig.mode,
                    index = this.$artifact.getIndex(this.showSymbol);
                this.ArtifactsList[index]; // 
                if (mode === "string") {
                    return this.$artifact.ArtifactScore(index, this.userSetting.scoreConfig.strRule).toFixed(2);
                } else if (mode === "array") {
                    return this.$artifact.ArtifactScore(index, this.userSetting.scoreConfig.arrRule).toFixed(2);
                } else {
                    return 0;
                }
            },
            language() {
                return this.state.language;
            }
        },
        watch: {
            showSymbol(val) {
                if (val === "") {
                    return;
                }
                // this.setRadarChart();
                this.showDetail = this.$artifact.getArtifact(val, this.userSetting.language);
            },
            ArtifactsList() {
                // this.setRadarChart();
                if (this.showSymbol !== "") this.showDetail = this.$artifact.getArtifact(this.showSymbol, this
                    .userSetting.language);
            },
            userSetting: {
                handler(val) {
                    if (this.showSymbol !== "" && val.filterPart !== "default" && this.$artifact.AUSList[this.$artifact
                            .getIndex(this.showSymbol)]
                        .part !== val.filterPart) this.showSymbol = "";
                    if (this.showSymbol !== "" && val.filterMain !== "default" && this.$artifact.AUSList[this.$artifact
                            .getIndex(this.showSymbol)]
                        .mainEntry !== val.filterMain) this.showSymbol = "";
                    this.changeSetting();
                    this.syncListData();
                },
                deep: true
            },
            language(val) {
                this.userSetting.language = val;
            }
        },
        methods: {
            // 
            createArtifact() {
                this.$artifact.createArtifact();
                // this.$artifact.batchCreate(100);
                this.syncListData();
                this.alertControl("", 1500);
            },
            // 
            batchCreate(count) {
                this.$artifact.batchCreate(count);
            },
            // 
            cusCreatArtifact() {
                let cusEntryValue = [],
                    cusEntry = [];
                for (let i = 0; i < this.cusEntry.length; i++) {
                    cusEntry.push(this.cusEntry[i]);
                    cusEntryValue.push(this.cusEntryRate[cusEntry[i]]);
                }
                this.$artifact.createArtifact(this.cusPart, this.cusMainEntry, cusEntry, cusEntryValue, this.cusSuit);
                this.syncListData();
                this.alertControl("", 1500);
            },
            // 
            ArtifactUpgrade(index, entry = "") {
                let res = this.$artifact.upgrade(index, entry, this.userSetting.entryQuality),
                    qualityAlert = "";
                if (Number.parseFloat(this.userSetting.entryQuality) !== -1) qualityAlert = ""
                this.syncListData();
                if (res === true) {
                    this.alertControl(this.$t('handle.upSuccess') + `${qualityAlert}`, 1500);
                } else {
                    this.alertControl(this.$t('handle.maxLv'), 1500, "warning");
                }
            },
            // 
            ArtifactRate(index) {
                let mode = this.userSetting.scoreConfig.mode;
                if (mode === "string") {
                    return this.$artifact.ArtifactScore(index, this.userSetting.scoreConfig.strRule);
                } else if (mode === "array") {
                    return this.$artifact.ArtifactScore(index, this.userSetting.scoreConfig.arrRule);
                } else {
                    return 0;
                }
            },
            // 
            initArtifact(index) {
                let res = this.$artifact.reset(index);
                this.syncListData();
                if (res) {
                    this.alertControl("~", 1500);
                } else {
                    this.alertControl("", 1500, "warning");
                }
            },
            // 
            ArtifactClear() {
                if (this.$artifact.AUSList.length === 0) {
                    this.alertControl("", 1500, "warning");
                } else if (confirm("\n****\n")) {
                    this.showSymbol = "";
                    this.$artifact.clearAll();
                    this.syncListData();
                }
            },
            // 
            deleteArtifact(index) {
                let res = this.$artifact.deleteOne(index);
                this.syncListData();
                if (res) {
                    this.showSymbol = "";
                    this.alertControl("", 1500);
                } else {
                    this.alertControl("", 1500, "warning");
                }
            },
            // 
            undoDel() {
                let res = this.$artifact.undoDel();
                this.syncListData();
                if (res === true) {
                    this.alertControl("", 1500);
                } else {
                    this.alertControl("", 1500, "primary");
                }
            },
            // 
            resetAll() {
                this.$artifact.resetAll();
                this.syncListData();
                this.alertControl("~", 1500);
            },
            // 
            changeSetting() {
                // 
                this.$store.commit('language', this.userSetting.language);
                this.$i18n.locale = this.$store.state.language;
                if (!window.localStorage) {
                    alert("localstorage");
                    return false;
                } else {
                    localStorage.userSetting = JSON.stringify(this.userSetting);
                }
            },
            // 
            clearStorge() {
                if (confirm("\n")) {
                    localStorage.clear();
                    this.$artifact.AUSList.length = 0;
                    this.ArtifactsList.length = 0;
                    location.reload();
                }
            },
            // 
            resetSetting() {
                localStorage.removeItem("userSetting");
                localStorage.userSetting = this.defaultSetting;
                this.userSetting = JSON.parse(this.defaultSetting);
                this.alertControl("", 1500);
            },
            changeLanguage(language) {
                this.userSetting.language = language;
            },
            // -
            // state success/primary/warning/danger
            alertControl(msg, time = 2000, state = "success") {
                this.alertFunc.alertMsg = msg;
                this.alertFunc.alertState = state;
                this.alertFunc.alertShow = true;
                clearTimeout(this.alertFunc.alertClose);
                this.alertFunc.alertClose = setTimeout(() => {
                    this.alertFunc.alertShow = false;
                }, time)
            },
            // 
            mainEntryfilter(val) {
                this.userSetting.filterMain = val;
            },
            // 
            sortList(index) {
                let sortMethod = ["lvasc", "lvdesc", "part", "main"];
                // symbol = this.showSymbol;
                this.$artifact.sortList(sortMethod[index]);
                this.syncListData();
            },
            // symbol
            changeShowSymbol(symbol) {
                this.showSymbol = symbol;
                this.$store.state.selectHistory = symbol;
                this.syncListData();
            },
            // 
            syncListData() {
                if (this.userSetting.language === "en") {
                    this.ArtifactsList = this.$artifact.getList("en", this.userSetting.filterPart, this.userSetting
                        .filterMain);
                } else if (this.userSetting.language === "zh") {
                    this.ArtifactsList = this.$artifact.getList("zh", this.userSetting.filterPart, this.userSetting
                        .filterMain);
                } else {
                    this.ArtifactsList = this.$artifact.getList("origin", this.userSetting.filterPart, this.userSetting
                        .filterMain);
                }
            },
            // /
            lockChange(index) {
                this.$artifact.lock(index);
                this.syncListData();
            },
            // 
            imgUrl(symbol) {
                let index = this.$artifact.getIndex(symbol),
                    item = this.$artifact.AUSList[index],
                    src = require('../assets/images' + "/" + item.suit.replace(/\s+/g, "") + "/" + item.part + ".png");
                return src;
            },

        }
    }
</script>

