<script>
    import 'bootstrap/js/dist/offcanvas'
    // import {getCurrentInstance} from "vue";

    export default {
        data() {
            return {
                title: "",
                loadProgress: 0,
                isLoad: false, // isLoad
                // 
                imgList: [require("@/assets/images/genshin-symbol.png"),
                require("@/assets/images/fog.png")
                ]
            }
        },
        mounted() {
            this.title = this.$t('title');
            this.loading();
            // ()
            // this.$axios.all([this.preLoadImg()])
            //     .then(this.$axios.spread(() => {

            //     })).catch((err => {
            //         console.log(err);
            //     }))
        },
        computed: {
            language() {
                return this.$store.state.language;
            }
        },
        watch: {
            language() {
                this.title = this.$t('title');
            },
            loadProgress(val) {
                let that = this;
                if (val == 100) {
                    setTimeout(function () {
                        // that.loadProgress = 100;
                        that.isLoad = true;
                    }, 500)
                }
            }
        },
        methods: {
            loading() {
                let that = this;
                // 
                document.fonts.ready.then(function () {
                    that.loadProgress += 20;
                });
                for(let src of this.imgList){
                    let img = new Image();
                    img.src = src;
                    img.onload = () => {
                        this.loadProgress += 40;
                    }
                    img.onerror = () => {
                        this.loadProgress += 40;
                        alert("\nSome resources failed to load, please refresh and try again!")
                    }
                }
            },
            preLoadImg() {
                const that = this;
                let errtime = 0,
                    request = this.$axios.get(that.$store.state.symbolSrc)
                    .then(() => {
                        that.loadProgress += 80;
                        return true;
                    }).catch(err => {
                        console.log(err);
                        (errtime < 3) ? (request, errtime++) : (that.loadProgress += 15);
                    })
                    // console.log(that.$store.state.symbolSrc)
                    console.log(that.imgList[0])
            }
        }
    };
</script>

